import { createMuiTheme } from 'material-ui/styles'
import { grey } from 'material-ui/colors'

const theme = createMuiTheme({
  palette: {
    primary: {
      ...grey,
      '500': '#424242'
    },
  },
  mixins: {
    toolbar: {
      minHeight: '3.5em',
    },
  },
  typography: {
    body1: {
      color: '#000',
    },
    title: {
      fontWeight: 300,
      fontSize: '1.2em',
    },
  },
})

export default theme



